import React from 'react';

import {
  safeNumber
} from '../settings/conversions';

var size = 400;
var half = size / 2;

export default class CompassReadingComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 10,
    };
  }

  render() {
    const angle = safeNumber(this.state.value);
    const angleText = (angle !== this.state.value) ? 'NaN' : angle.toFixed(0);
    return (
        <svg height="100%" width="100%" viewBox="100 0 200 100">
          <g transform={centerRotate(-angle)}>
            {tenTicks}
            {fiveTicks}
            {mainDirs}
            {minorDirs}
            {(angleText !== 'NaN') ? needle() : undefined}
          </g>
          <text x="200" y="70" textAnchor="middle" fontSize="38" dominantBaseline="middle" stroke="none">{angleText}</text>
          <text x="200" y="90" textAnchor="middle" fontSize="12" dominantBaseline="middle" stroke="none">{this.props.options.label}</text>
        </svg>
    )
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if(nextProps.valueStream !== this.props.valueStream) {
      if (nextProps.valueStream) {
        if (this.unsubscribe) {
          this.unsubscribe();
        }
        this.unsubscribe = nextProps.valueStream.onValue(function(value) {
          this.setState({
            value: value
          });
        }.bind(this));
      }
    }
  }

  componentDidMount() {
    if (this.props.valueStream) {
      if (this.unsubscribe) {
        this.unsubscribe();
      }
      this.unsubscribe = this.props.valueStream.onValue(function(value) {
        this.setState({
          value: value
        });
      }.bind(this));
    }
  }

  componentWillUnmount() {
    if(this.unsubscribe) {
      this.unsubscribe();
    }
  }
};



function centerRotate(deg) {
  return "rotate(" + deg + " " + half + " " + half + ")"
}

function create10Ticks() {
  var result = []
    for (var i = 0; i < 360; i += 10) {
    if (i % 90 != 0) {
      result.push(
        <path key={"id10-" + i} id={"id10-" + i} d={"m " + half + "," + "0 L " + half + "," + (half * 0.1)} strokeWidth="1" transform={centerRotate(i)}/>
      )
    }
  }
  return result;
}

function create5Ticks() {
  var result = []
  for (var i = 5; i < 360; i += 10) {
    if (i % 45 != 0) {
      result.push(
        <path key={"id5-" + i} id={"id5-" + i} d={"m " + half + "," + "0 L " + half + "," + (half * 0.07)} strokeWidth="1"  transform={centerRotate(i)}/>
      )
    }
  }
  return result;
}

function toMainDirText(dir, i) {
  return (
    <text id={'bearing' + i*90}
       key={'bearing' + i*90}
       x={half}
       y={0.06 * size * 0.8}
       textAnchor="middle"
       fontSize={(0.07 * size) + "px"}
       transform={centerRotate(i*90)} stroke="none">{dir}</text>
  )
}

function toMinorDirText(dir, i) {
  return (
    <text id={'bearing' + i*90+45}
       key={'bearing' + i*90+45}
       x={half}
       y={0.03 * size * 0.8}
       textAnchor="middle"
       fontSize={(0.03 * size) + "px"}
       transform={centerRotate(i*90+45)} stroke="none">{dir}</text>
  )
}

function needle() {
  return (
    <path key="needle" d="M 200,25 l 10,10 L 210,375 l -20,0 L 190,35 Z" style={{fill:'none', strokeWidth:1}}></path>
  )
}

var tenTicks = create10Ticks();
var fiveTicks = create5Ticks();
var mainDirs = ['N', 'E', 'S', 'W'].map(toMainDirText);
var minorDirs = ['NE', 'SE', 'SW', 'NW'].map(toMinorDirText);
var mainDirs = ['N', 'E', 'S', 'W'].map(toMainDirText);
var minorDirs = ['NE', 'SE', 'SW', 'NW'].map(toMinorDirText);
